*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: linear-gradient(to right, #88ffa2, #ff5555)
}

.calculator-grid {
  display: grid;
  
  margin-top: 1.5rem;
  justify-content: center;
  grid-template-columns: repeat(4, 6rem);
  grid-template-rows: minmax(7rem, auto) repeat(6, 6rem);
}

.calculator-grid > button {
  cursor: pointer;
  font-size: 2rem;
  border: 1px solid rgb(163, 163, 163);
  outline: none;
  background-color: rgba(255,255,255,.75);
}

.calculator-grid > button:hover,
.calculator-grid > button:focus {
  background-color: rgba(255,255,255,.9);
}

.weight-grid {
  display: grid;
  /* margin-top: 2rem; */
  justify-content: center;
  grid-template-columns: repeat(4, 6rem);
  grid-template-rows: minmax(7rem, auto) repeat(6, 6rem);
  

  position: relative; 
  top: -95px;
}

.weight-grid > button {
  cursor: pointer;
  font-size: 2rem;
  border: 1px solid rgb(163, 163, 163);
  outline: none;
  border-radius:0px 0px 25px 25px;
  background-color: rgba(209, 74, 74, 0.75);
}

.weight-grid > button:hover,
.weight-grid > button:focus {
  background-color: rgba(255, 255, 255, 0.9);
}

.fourtyfive {
  background-color:rgba(32, 3, 3, 1)
}

.span-two {
  grid-column: span 2;
}

.output {
  grid-column: 1/-1;
  background-color: rgba(0,0,0,0.5);
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  padding: .75rem;
  word-wrap: break-word;
  word-break: break-all;
  border-radius:25px 25px 0px 0px;
}

.output .previous-operand {
  color: rgba(255,255,255,.75);
  font-size: 1.5rem;
}

.output .current-operand {
  color: white;
  font-size: 2.5rem;
}

